































































































































































































































































































import { Vue, Component } from 'vue-property-decorator';
import { BaseLayoutComponentData } from '@/types/layouts/baseLayoutComponent.interface';
import { mapState } from 'vuex';

@Component({
  computed: {
    ...mapState([
      'title', // 当前页面标题
      'breadcrumbs', // 当前页面面包屑导航
      'overlay', // 页面遮罩层状态
      'loading', // 页面加载状态 / HTTP请求加载状态
      'skeletonLoader' // 页面骨架加载器
    ])
  },

  data(): BaseLayoutComponentData {
    return {
      drawer: null, // 抽屉展开收起状态
      miniVariant: false, // 是否使用迷你抽屉
      expandOnHover: false, // 将抽屉折叠成 mini-variant，直到用鼠标悬停
      navMenuItems: [] // 抽屉菜单列表项
    };
  }
})
export default class BaseLayoutAdmin extends Vue {
  title!: string; // 当前页面标题
  breadcrumbs!: object[]; // 当前页面面包屑导航
  overlay!: boolean; // 页面遮罩层状态
  loading!: boolean; // 页面加载状态 / HTTP请求加载状态
  skeletonLoader!: boolean; // 页面骨架加载器

  drawer!: boolean | null; // 抽屉展开收起状态
  miniVariant!: boolean; // 是否使用迷你抽屉
  expandOnHover!: boolean; // 将抽屉折叠成 mini-variant，直到用鼠标悬停
  navMenuItems!: object[]; // 抽屉菜单列表项

  // 页面遮罩 / 加载 / HTTP请求总状态
  get pageLoading() {
    if (!this.overlay && !this.loading) {
      // 遮罩/加载都为false时，加载才关闭
      return false;
    }
    return true;
  }
  // 页面标题相关
  get pageTitle() {
    return this.$route.meta.isDynamicTitle
      ? this.title
      : this.$route.meta.title;
  }
  // 页面面包屑导航相关
  get pageBreadcrumbs() {
    return this.$route.meta.isDynamicTitle
      ? this.breadcrumbs
      : this.$route.meta.breadcrumbs;
  }

  // 抽屉菜单相关
  drawerActive() {
    if(this.$vuetify.breakpoint.lgAndUp) {
      this.miniVariant = !this.miniVariant;
    } else {
      this.miniVariant = false;
      this.drawer = !this.drawer;
    }
  }
  drawerHover(event: any) {
    // console.log(event.type)
    if (this.miniVariant) {
      switch (event.type) {
        case 'mouseover':
          this.expandOnHover = true;
          break;
        case 'mouseleave':
          this.expandOnHover = false;
          break;
      }
    }
  }
  // 获取抽屉菜单后台数据
  fetchNavMenuItemsData() {
    this.navMenuItems = [
      {
        // icon: "mdi-shape-outline",
        icon: "D3III16glBasic icon-16gl-appsBig",
        // icon: "D3III16glMobius icon-16gl-B",
        title: "基础（Basic）",
        children: [
          { title: "颜色（Colors）", path: "/components/colors" },
          { title: "排版（Typography）", path: "/components/typography" },
          { title: "图标（Icons）", path: "/components/icons" },
          { title: "按钮（Buttons）", path: "/components/buttons" }
        ]
      },
      {
        // icon: "mdi-table-row",
        icon: "D3III16glBasic icon-16gl-rename",
        // icon: "D3III16glMobius icon-16gl-C",
        title: "栏（Bars）",
        children: [
          { title: "顶部应用栏（App bars: top）", path: "/components/app-bars" },
          { title: "底部应用栏（App bars: bottom）", path: "/components/app-bars-bottom" }
        ]
      },
      {
        // icon: "mdi-menu",
        icon: "D3III16glBasic icon-16gl-mapMarker",
        // icon: "D3III16glMobius icon-16gl-D",
        title: "导航（Navigation）",
        children: [
          { title: "导航抽屉（Navigation Drawer）", path: "/components/navigation-drawer" },
          { title: "底部导航（Bottom Navigation）", path: "/components/bottom-navigation" },
          { title: "选项卡（Tabs）", path: "/components/tabs" },
          { title: "菜单（Menus）", path: "/components/menus" },
          { title: "面包屑（Breadcrumbs）", path: "/components/breadcrumbs" }
        ]
      },
      {
        // icon: "mdi-clipboard-check-outline",
        icon: "D3III16glBasic icon-16gl-clipboardCheck",
        // icon: "D3III16glMobius icon-16gl-E",
        title: "表单（Forms）",
        children: [
          { title: "单选按钮（RadioButtons）", path: "/components/radio-buttons" },
          { title: "复选框（Checkboxes）", path: "/components/checkboxes" },
          { title: "开关（Switches）", path: "/components/switches" },
          { title: "文本框（TextFields）", path: "/components/text-fields" },
          { title: "多行文本域（Textareas）", path: "/components/textareas" },
          { title: "下拉选择框（Selects）", path: "/components/selects" }
        ]
      },
      {
        // icon: "mdi-table",
        icon: "D3III16glBasic icon-16gl-code",
        // icon: "D3III16glMobius icon-16gl-F",
        title: "数据（Datas）",
        children: [
          { title: "数据表格（DataTables）", path: "/components/data-tables" },
          { title: "分页（Paginations）", path: "/components/paginations" },
          { title: "列表（Lists）", path: "/components/lists" },
          { title: "头像（Avatars）", path: "/components/avatars" },
          { title: "徽章标记（Badges）", path: "/components/badges" }
        ]
      },
      // {
      //   icon: "mdi-image-outline",
      //   title: "图像（Images）",
      //   children: [
      //     { title: "图片（Images）", path: "/components/images" },
      //     { title: "颜色（Color）", path: "/components/" },
      //     { title: "颜色（Color）", path: "/components/" }
      //   ]
      // },
      {
        // icon: "mdi-message-alert-outline",
        icon: "D3III16glBasic icon-16gl-bubblesDots2",
        // icon: "D3III16glMobius icon-16gl-G",
        title: "通知（Notice）",
        children: [
          { title: "警告（Alerts）", path: "/components/alerts" },
          { title: "消息条（Snackbars）", path: "/components/snackbars" },
          { title: "横幅消息（Banners）", path: "/components/banners" },
          { title: "对话框（Dialogs）", path: "/components/dialogs" }
        ]
      },
      {
        // icon: "mdi-dots-horizontal",
        icon: "D3III16glBasic icon-16gl-ellipsis",
        // icon: "D3III16glMobius icon-16gl-H",
        title: "其他（Others）",
        children: [
          { title: "卡片（Cards）", path: "/components/cards" },
          { title: "小纸片（Chips）", path: "/components/chips" },
          { title: "图片（Images）", path: "/components/images" },
          { title: "工具提示（Tooltips）", path: "/components/tooltips" }
        ]
      }
    ];
  }

  // // 路由跳转相关
  // goToPage (item: any) {
  //   // 如果跳转路由和当前路由一致，则不跳转
  //   if (this.$route.path === item.path) {
  //     return
  //   }
  //   this.$router.push({
  //     path: item.path
  //   });
  // }

  // // 返回页面顶部按钮相关
  // pageYOffset = 0; // 页面的垂直偏移值
  // getPageYOffset() {
  //   // console.log(window.pageYOffset);
  //   this.pageYOffset =
  //     window.pageYOffset ||
  //     document.documentElement.scrollTop ||
  //     document.body.scrollTop;
  // }

  // 生命周期钩子函数
  created() {
    this.fetchNavMenuItemsData();
  }
}
